/* src/index.css */

/* Optional: Basic reset */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* Optional: Global styles */
body {
  font-family: Arial, sans-serif;
  background-color: #f4f4f4;
  color: #333;
}

/* Optional: Styling for the root div */
#root {
  padding: 20px;
}
